@import '@style/index';

.calendarWrap{
  border-bottom:1px solid $gray200;
  padding-bottom:13px;
  & .year{
    width:192px;
  }
  & .months{
    width:calc(100% - 192px);
    & li{
      @include font(1.6,500,$gray800);
      cursor: pointer;
      &.active{
        position:relative;
        border:1px solid #000;
        border-radius:8px;
        background-color:$gray50;
        padding:4px 6px;
      }
      &.disabled{
        cursor:inherit;
        @include font(1.6,500,$gray200);
      }
    }
  }
}